.page {
    font-family: Poppins, sans-serif;
    scroll-behavior: smooth;
    color: $main-text-color;

    &__body {
        margin: 0;
    }
      
    &__menu {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        opacity: 0;
        pointer-events: none;

    &:target {
        opacity: 1;
        pointer-events: all;
    }
  }
}
